

@import '../shared';

#screenshots {
    margin: 0 -10px;

    font-size: 0;

    .img-wrapper {
        display: inline-block;
        vertical-align: top;
        width: 50%;
        padding: 0 10px;
        margin-bottom: 20px;

        @media (min-width: 1400px) {
            width: 33.33%;
        }

        img {
            width: 100%;
            height: auto;

            border: 4px solid nth($dark-colors, 7);
        }
    }
}




