

@import '../shared';

#experiences {
    .job {
        margin-bottom: 30px;

        .company {
            display: inline-block;
            margin-right: 10px;

            font-weight: 700;
            text-transform: uppercase;
            color: #444;
        }

        .period {
            display: inline-block;

            font-size: 12px;
            font-weight: 600;
            text-transform: uppercase;
        }

        .tag {
            display: inline-block;
            padding: 2px 6px;
            margin-bottom: 10px;

            font-size: 12px;

            background-color: nth($dark-colors, 1);
            color: white;
            border-radius: 5px;

            & + .tag {
                margin-left: 4px;
            }
        }
    }
}




