

@import '../shared';

#photo-and-name {
    padding: 20px;

    text-align: center;
    color: white;
    background-color: nth($dark-colors, 3);

    .photo {
        box-sizing: border-box;
        width: 120px;
        height: 120px;
        margin: 0 auto;
        overflow: hidden;

        border: 4px solid white;
        border-radius: 120px;
    }

    .job-title {
        margin-top: 10px;

        font-size: 12px;
        font-weight: 300;
        text-transform: uppercase;
        opacity: .7;
    }

    .name {
        font-size: 20px;
        font-weight: 600;
    }

    @media (max-width: 979px) {
        .content {
            width: 360px;
            height: 130px;
            margin: 0 auto;
        }

        .photo {
            float: left;
        }

        .job-title {
            font-size: 14px;
            padding-top: 24px;
        }

        .name {
            font-size: 24px;
        }
    }

}




