

@import '../shared';

#demo {
    margin: 0 -10px;

    font-size: 0;

    .img-wrapper {
        position: relative;
        display: inline-block;
        vertical-align: top;
        width: 50%;
        padding: 0 10px;
        margin-bottom: 20px;

        @media (min-width: 1400px) {
            width: 33.33%;
        }

        &:hover:before {
            position: absolute;
            top: 0;
            bottom: 0;
            left: 10px;
            right: 10px;
            z-index: 1;

            padding-top: 20%;

            content: 'View Online Demo';
            font-size: 30px;
            font-weight: bold;
            text-align: center;

            color: white;
            background: rgba(nth($dark-colors, 6), .9);
        }

        img {
            width: 100%;
            height: auto;

            border: 4px solid nth($dark-colors, 6);
        }
    }
}




