

#skills {
    .skill {
        float: left;
        box-sizing: border-box;
        width: 50%;
        min-height: 100px;
        margin-bottom: 30px;

        &:nth-child(2n+1) { padding-right: 15px; }
        &:nth-child(2n) { padding-left: 15px; }

        @media (min-width: 1600px) {
            width: 25%;
            padding: 0 10px;

            &:first-child { padding-left: 0; padding-right: 20px; }
            &:last-child  { padding-left: 20px; padding-right: 0; }
        }

        .title {
            font-size: 20px;
            font-weight: 700;
            white-space: nowrap;
        }
    }

    .technique-title {
        clear: both;
        margin-bottom: 10px;

        font-size: 18px;
        font-weight: 700;
    }

    .technique-list {
        margin: 0 -20px;
    }
}




