

@import '../shared';

.hint {
    position: absolute;
    top: 10px;
    right: 60px;
    display: none;

    font-size: 12px;

    @media (min-width: 980px) {
        display: block;
    }

}

#app-body {
    font-size: 16px;

    color: #777;
    overflow-x: hidden;
    overflow-y: auto;

    transition: all .3s ease;
}




